// https://www.npmjs.com/package/react-native-numeric-input
import React, { useEffect, useState } from "react";
import {
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from "react-native";
import { allItems, resources } from "./ItemInfo";
import { Picker } from "@react-native-picker/picker";

import NumericInput from "./Components/NumericInput";

export default function App() {
  interface Ingredient {
    name: string;
    amount: number;
    depth: number;
    numberOfBuildings: number;
    building: string;
  }

  interface Resources {
    Name: string;
    Amount: number;
    Building: string;
  }

  const [treeView, setTreeView] = useState(true);
  const [currentItem, setCurrentItem] = useState("Wood Plank");
  const [amount, setAmount] = useState(1);
  const [ingredients, setIngredients] = useState<Ingredient[]>([]);
  const [workshopLevel, setWorkShopLevel] = useState(1);
  const [furnaceLevel, setFurnaceLevel] = useState(1);
  const [machineShopLevel, setMachineShopLevel] = useState(1);
  const [industrialFactoryLevel, setIndustrialFactoryLevel] = useState(1);
  const [forgeLevel, setForgeLevel] = useState(1);
  const [manufacturerLevel, setManufacturerLevel] = useState(1);
  const [extractorLevel, setExtractorLevel] = useState(1);
  const [rawResources, setRawResources] = useState<Resources[]>([]);

  const [maxItemPerMin, setMaxItemPerMin] = useState(0);
  const [woodAmount, setWoodAmount] = useState(1000);
  const [stoneAmount, setStoneAmount] = useState(1000);
  const [ironAmount, setIronAmount] = useState(1000);
  const [copperAmount, SetCopperAmount] = useState(1000);
  const [wolframiteAmount, setWolframiteAmount] = useState(1000);
  const [coalAmount, setCoalAmount] = useState(1000);

  var ingList: Ingredient[] = [];
  var resourceCount: Resources[] = [];

  // If any params are updated then we want to recalculate the list
  useEffect(() => {
    ingList = [];
    resourceCount = [];

    const ingInfo = allItems.find((item) => {
      return item.name == currentItem;
    });

    const buildingLevel = getBuildingLevel(ingInfo!.building);
    const multiplier = levelMultiplier(buildingLevel!);
    const numberOfBuildings = amount / ingInfo!.itemsPerMin / multiplier;

    var building = ingInfo!.building;
    addIng(currentItem, amount, 0, numberOfBuildings, building);
    GenerateList(currentItem, amount);
    setIngredients(ingList);
    setRawResources(resourceCount);
  }, [
    workshopLevel,
    furnaceLevel,
    machineShopLevel,
    industrialFactoryLevel,
    forgeLevel,
    manufacturerLevel,
    extractorLevel,
    amount,
    currentItem,
  ]);

  useEffect(() => {
    let maxOutput = Infinity;
    resources.forEach((resource) => {
      let resourceAmount = 0;
      rawResources.forEach((rawResource) => {
        if (rawResource.Name == resource) {
          resourceAmount = rawResource.Amount;
        }
      });

      if (resourceAmount > 0) {
        let ipm = getResourceAmount(resource) / resourceAmount;
        if (ipm < maxOutput) {
          maxOutput = ipm;
        }
      }
    });

    setMaxItemPerMin(maxOutput * amount);
  }, [rawResources, woodAmount, stoneAmount, ironAmount, copperAmount, coalAmount, wolframiteAmount]);

  const addIng = (
    name: string,
    amount: number,
    depth: number,
    numberOfBuildings: number,
    building: string
  ) => {
    // Add to the sum list
    var itemInArray = false;
    resourceCount.forEach((element) => {
      if (element.Name == name) {
        element.Amount += amount;
        itemInArray = true;
      }
    });

    if (!itemInArray) {
      resourceCount.push({
        Name: name,
        Amount: amount,
        Building: building,
      });
    }

    ingList.push({
      name: name,
      amount: amount,
      depth: depth,
      numberOfBuildings: Math.ceil(numberOfBuildings),
      building: building,
    });
  };

  function getResourceAmount(name: string) {
    switch (name.toLowerCase()) {
      case "wood log":
        return woodAmount;
      case "stone":
        return stoneAmount;
      case "iron ore":
        return ironAmount;
      case "copper ore":
        return copperAmount;
      case "wolframite":
        return wolframiteAmount;
      case "coal":
        return coalAmount;
      default:
        return 0;
    }
  }

  function getBuildingLevel(name: string) {
    switch (name.toLowerCase()) {
      case "workshop":
        return workshopLevel;
      case "furnace":
        return furnaceLevel;
      case "machine shop":
        return machineShopLevel;
      case "industrial factory":
        return industrialFactoryLevel;
      case "forge":
        return forgeLevel;
      case "manufacturer":
        return manufacturerLevel;
      case "extractor":
        return extractorLevel;
      case "earth transporter":
        return 1;
    }
  }

  function levelMultiplier(level: number) {
    switch (level) {
      case 1:
        return 1;
      case 2:
        return 1.5;
      case 3:
        return 2;
      case 4:
        return 3;
      case 5:
        return 4;
      default:
        return 1;
    }
  }

  function GenerateList(name: string, amountPerMin = 1, depth = 1) {
    const itemInfo = allItems.find((item) => {
      return item.name == name;
    });

    itemInfo?.ingredientList.forEach((ingredient) => {
      const requireAmountPerMin = ingredient.amount * amountPerMin;
      const buildingLevel = getBuildingLevel("extractor");
      const multiplier = levelMultiplier(buildingLevel!);
      const numberOfBuildings = Math.ceil(
        requireAmountPerMin / (7.5 * multiplier)
      );
      if (resources.includes(ingredient.name)) {
        addIng(
          ingredient.name,
          requireAmountPerMin,
          depth,
          numberOfBuildings,
          "Extractor"
        );
      } else {
        const ingInfo = allItems.find((item) => {
          return item.name == ingredient.name;
        });
        const buildingLevel = getBuildingLevel(ingInfo!.building);
        const multiplier = levelMultiplier(buildingLevel!);
        const numberOfBuildings =
          requireAmountPerMin / ingInfo!.itemsPerMin / multiplier;
        addIng(
          ingredient.name,
          requireAmountPerMin,
          depth,
          numberOfBuildings,
          ingInfo!.building
        );
        GenerateList(ingredient.name, requireAmountPerMin, depth + 1);
      }
    });
  }

  function renderList() {
    return (
      <View style={{ alignItems: "flex-start" }}>
        {ingredients.map((data) => {
          if (data.amount == 0) {
            return null;
          }
          return (
            <Text style={{ paddingLeft: data.depth * 10 }}>
              {data.amount} {data.name} ({data.numberOfBuildings}{" "}
              {data.building})
            </Text>
          );
        })}
      </View>
    );
  }

  // We should give these elements an id
  function renderIngList() {
    return (
      <View style={{ alignItems: "flex-start" }}>
        {rawResources
          .sort((a, b) => {
            return a.Name.localeCompare(b.Name);
          })
          .map((data) => {
            var itemsPerMin = 0;
            if (resources.includes(data.Name)) {
              itemsPerMin =
                7.5 * levelMultiplier(getBuildingLevel("extractor")!);
            } else {
              const ingInfo = allItems.find((item) => {
                return item.name == data.Name;
              });
              itemsPerMin = ingInfo!.itemsPerMin;
            }

            const buildingLevel = getBuildingLevel(data.Building);

            const multiplier = levelMultiplier(buildingLevel!);
            const numberOfBuildings = data.Amount / itemsPerMin / multiplier;
            return (
              <Text>
                {data.Name}: {data.Amount} ({Math.ceil(numberOfBuildings)}{" "}
                {data.Building})
              </Text>
            );
          })}
      </View>
    );
  }

  return (
    <View style={{ alignItems: "center" }}>
      <View style={{ flexDirection: "row", alignItems: "center", padding: 5, flexWrap: 'wrap', justifyContent: 'center'  }}>
        <NumericInput
          title={"Extractor"}
          width={100}
          height={30}
          value={extractorLevel.toString()}
          onChange={(value: number) => {
            setExtractorLevel(value);
          }}
          max={5}
        />
        <NumericInput
          title={"Workshop"}
          width={100}
          height={30}
          value={workshopLevel.toString()}
          onChange={(value: number) => {
            setWorkShopLevel(value);
          }}
          max={4}
        />
        <NumericInput
          title={"Furnace"}
          width={100}
          height={30}
          value={furnaceLevel.toString()}
          onChange={(value: number) => {
            setFurnaceLevel(value);
          }}
          max={4}
        />
        <NumericInput
          title={"Machine Shop"}
          width={100}
          height={30}
          value={machineShopLevel.toString()}
          onChange={(value: number) => {
            setMachineShopLevel(value);
          }}
          max={4}
        />
        <NumericInput
          title={"Industrial Factory"}
          width={100}
          height={30}
          value={industrialFactoryLevel.toString()}
          onChange={(value: number) => {
            setIndustrialFactoryLevel(value);
          }}
          max={4}
        />
        <NumericInput
          title={"Forge"}
          width={100}
          height={30}
          value={forgeLevel.toString()}
          onChange={(value: number) => {
            setForgeLevel(value);
          }}
          max={4}
        />
        <NumericInput
          title={"Manufacturer"}
          width={100}
          height={30}
          value={manufacturerLevel.toString()}
          onChange={(value: number) => {
            setManufacturerLevel(value);
          }}
          max={4}
        />
      </View>
      <View style={{ flexDirection: "row", alignItems: "center", padding: 5, flexWrap: 'wrap', justifyContent: 'center' }}>
        <NumericInput
            title={"Wood"}
            width={100}
            height={30}
            value={woodAmount.toString()}
            onChange={(value: number) => {
              setWoodAmount(value);
            }}
            max={100000}
          />
          <NumericInput
            title={"Stone"}
            width={100}
            height={30}
            value={stoneAmount.toString()}
            onChange={(value: number) => {
              setStoneAmount(value);
            }}
            max={100000}
          />
          <NumericInput
            title={"Copper"}
            width={100}
            height={30}
            value={copperAmount.toString()}
            onChange={(value: number) => {
              SetCopperAmount(value);
            }}
            max={100000}
          />
          <NumericInput
            title={"Iron"}
            width={100}
            height={30}
            value={ironAmount.toString()}
            onChange={(value: number) => {
              setIronAmount(value);
            }}
            max={100000}
          />
          <NumericInput
            title={"Coal"}
            width={100}
            height={30}
            value={coalAmount.toString()}
            onChange={(value: number) => {
              setCoalAmount(value);
            }}
            max={100000}
          />
          <NumericInput
            title={"Wolframite"}
            width={100}
            height={30}
            value={wolframiteAmount.toString()}
            onChange={(value: number) => {
              setWolframiteAmount(value);
            }}
            max={100000}
          />
        </View>
      <View style={{ alignItems: "center" }}>
        <View
          style={{ flexDirection: "row", alignItems: "center", padding: 10 }}
        >
          <View>
            <Text>Max Output</Text>
          <TouchableOpacity 
          style={{
            height: 45,
            width: 100,
            borderColor: "black",
            borderRadius: 5,
            borderWidth: 1,
            alignItems: "center",
            justifyContent: "center",
          }}
          onPress={() => setAmount(maxItemPerMin)}>
            <Text style={{fontSize: 8}}>(Press Me)</Text>
          <Text>{maxItemPerMin.toFixed(2)}</Text>
        </TouchableOpacity>
        </View>
          <NumericInput
            title={"Items/Min"}
            width={150}
            height={45}
            value={amount.toString()}
            onChange={(value: number) => {
              setAmount(value);
            }}
            max={10000}
            showButtons={false}
          />
          <View style={{ width: 5 }} />

          <Picker
            style={{
              width: 100,
              padding: 10,
              borderWidth: 1,
              borderColor: "#666",
              borderRadius: 5,
              height: 40,
            }}
            selectedValue={currentItem}
            onValueChange={(itemValue, itemIndex) => setCurrentItem(itemValue)}
            itemStyle={{ borderColor: "red", borderWidth: 2, borderRadius: 5 }}
          >
            {allItems
              .sort((a, b) => {
                return a.name.localeCompare(b.name);
              })
              .map((data) => {
                return <Picker.Item label={data.name} value={data.name} />;
              })}
          </Picker>
        </View>
      </View>

      <View
        style={{
          flexDirection: "row",
          width: 500,
          alignSelf: "center",
          justifyContent: "center",
        }}
      >
        <TouchableOpacity
          style={{
            height: 50,
            width: "25%",
            borderColor: "black",
            borderRadius: 5,
            borderWidth: 1,
            alignItems: "center",
            justifyContent: "center",
            backgroundColor: treeView ? "#D3D3D3" : "white",
          }}
          onPress={() => {
            setTreeView(true);
          }}
        >
          <Text style={{ padding: 5 }}>Tree View</Text>
        </TouchableOpacity>
        <View style={{ width: 5 }} />
        <TouchableOpacity
          style={{
            height: 50,
            width: "25%",
            borderColor: "black",
            borderRadius: 5,
            borderWidth: 1,
            alignItems: "center",
            justifyContent: "center",
            backgroundColor: !treeView ? "#D3D3D3" : "white",
          }}
          onPress={() => {
            setTreeView(false);
          }}
        >
          <Text style={{ padding: 5 }}>Summary View</Text>
        </TouchableOpacity>
      </View>
      {treeView ? renderList() : renderIngList()}
      <View style={{ height: 50 }} />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    width: "100%",
    height: "50%",
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
    paddingBottom: 30,
  },
  buildingLevel: {
    paddingLeft: 15,
    paddingRight: 2,
  },
  buildingLevelInput: {
    width: 30,
    padding: 5,
    borderColor: "black",
    borderRadius: 5,
    borderWidth: 1,
    height: 30,
  },
  inputContainer: {
    lexDirection: "row", 
    alignItems: "center", 
    padding: 5, 
    flexWrap: 'wrap', 
    justifyContent: 'center'
  },
});
